export default {
  decodeUnicode(str) {
    if (str) {
      str = str.replace(/\\/g, "%");
      return unescape(str);
    }
  },
  encodeUnicode(str) {
    var res = [];
    for (var i = 0; i < str.length; i++) {
      res[i] = ("00" + str.charCodeAt(i).toString(16)).slice(-4);
    }
    return "\\u" + res.join("\\u");
  },

  /*设置cookie*/
  setCookie(name, value, time) {
    var setting = arguments[0];
    if (Object.prototype.toString.call(value).slice(8, -1) === 'Object') {
      var value = JSON.stringify(value);
    }
    if (Object.prototype.toString.call(setting).slice(8, -1) === 'Object') {
      for (var i in setting) {
        var oDate = new Date();
        oDate.setTime(oDate.getTime() + time);
        document.cookie = i + '=' + setting[i] + ';expires=' + oDate.toGMTString();

      }
    } else {
      var oDate = new Date();
      oDate.setTime(oDate.getTime() + time);
      document.cookie = name + '=' + value + ';expires=' + oDate.toGMTString();
    }

  },

  /*获取cookie*/
  getCookie(name) {
    function isJSON(str) {
      if (typeof str == 'string') {
        try {
          var obj = JSON.parse(str);
          if (typeof obj == 'object' && obj) {
            return true;
          } else {
            return false;
          }

        } catch (e) {
          return false;
        }
      }
    }
    var arr = document.cookie.split('; ');
    for (var i = 0; i < arr.length; i++) {
      var arr2 = arr[i].split('=');
      if (arr2[0] == name) {
        if (isJSON(arr2[1])) {
          return JSON.parse(arr2[1]);
        } else {
          return arr2[1];
        }

      }
    }

    return '';
  },

  /*删除cookie*/
  removeCookie(name) {
    this.setCookie(name, 1, -1);
  },


  /** 
   * 总容量5M 
   * 存入缓存，支持字符串类型、json对象的存储 
   * 页面关闭后依然有效 ie7+都有效 
   * @param key 缓存key 
   * @param stringVal 
   * @time 数字 缓存有效时间（秒） 默认60s  
   * 注：localStorage 方法存储的数据没有时间限制。第二天、第二周或下一年之后，数据依然可用。不能控制缓存时间，故此扩展 
   * */
  setLocal(key, stringVal, time) {
    try {
      if (!localStorage) { return false; }
      if (!time || isNaN(time)) { time = 60; }
      var cacheExpireDate = (new Date() - 1) + time * 1000;
      var cacheVal = { val:stringVal, exp:cacheExpireDate };
      localStorage.setItem(key, JSON.stringify(cacheVal)); //存入缓存值  
    } catch (e) { }
  },
  /**获取缓存*/
  getLocal(key) {
    try {
      if (!localStorage) { return false; }
      var cacheVal = localStorage.getItem(key);
      var result = JSON.parse(cacheVal);
      var now = new Date() - 1;
      if (!result) { return null; } //缓存不存在  
      if (now > result.exp) { //缓存过期  
        this.removeLocal(key);
        return "";
      }
      //console.log("get cache:"+key);  
      return result.val;
    } catch (e) {
      this.removeLocal(key);
      return null;
    }
  },
  /**移除缓存，一般情况不手动调用，缓存过期自动调用*/
  removeLocal(key) {
    if (!localStorage) { return false; }
    localStorage.removeItem(key);
  },
  /**清空所有缓存*/
  clear() {
    if (!localStorage) { return false; }
    localStorage.clear();
  },
  /*设置sessionStorage*/
  setSession(key, val) {
    var setting = arguments[0];
    if (Object.prototype.toString.call(setting).slice(8, -1) === 'Object') {
      for (var i in setting) {
        window.sessionStorage.setItem(i, JSON.stringify(setting[i]))
      }
    } else {
      window.sessionStorage.setItem(key, JSON.stringify(val))
    }

  },

  /*获取sessionStorage*/
  getSession(key) {
    if (key) return JSON.parse(window.sessionStorage.getItem(key))
    return null;

  },

  /*移除sessionStorage*/
  removeSession(key) {
    window.sessionStorage.removeItem(key)
  },

  /*移除所有sessionStorage*/
  clearSession() {
    window.sessionStorage.clear()
  }
}
