
function fn(name){
 return () => import('@/views/' + name)
}



export default new VueRouter({
  mode: 'history',
  routes: [
    {
      path: '/', 
      redirect: {
        name: 'manage'
      }
    },
    {
      path: '/', 
      name: 'manage',
      component: fn('manage'),
      redirect: {
        name: 'index'
      },
      meta: {
        title: '首页',
      },
      children: [{
        path: '',
        name: 'index',
        component: () => import('@/views/index'),
        meta: {
          title: '首页',
          ascription: "index"
        }
      },{
        path: '/space',  //空间
        name: 'space',
        component: () => import('@/views/space/index'),
        meta: {
          title: '部员',
          ascription: "space",
          Token: true
        }
      },{
        path: '/video/:pid',  //空间
        name: 'video',
        component: () => import('@/views/video/index'),
        meta: {
          title: '视频',
          ascription: "video"
        }
      }]
    },
    {
      path: '/login', //登录
      name: 'login',
      component: () => import('@/views/sso/login'),
      meta: {
        title: '登录',
       
      }
    },
    {
      path: '/register', //注册
      name: 'register',
      component: () => import('@/views/sso/register'),
      meta: {
        title: '注册'
      }
    },

    {
      path: '*', //404
      component: () => import('@/views/404'),
      meta: {
        title: '亲，没有找到该页面哦'
      }
    },

  ]
})
