import router from '@/router/router'
import store from '@/store/store'
import tool from '@/assets/js/tool'

// 基本配置

const baseURL = process.env.NODE_ENV === 'development' ? '/api' : '/api/dmb/v2';  

axios.defaults.baseURL  = Vue.prototype.baseURL = baseURL

axios.defaults.timeout = 30000
//添加一个请求拦截器
axios.interceptors.request.use(config => {
  if (config.method == 'get') {
    config.params = {
      t: Date.parse(new Date()) / 1000,
      ...config.params
    }
  }
  if (tool.getLocal('dmbID')) {  // 判断是否存在token，如果存在的话，则每个http header都加上token
    config.headers['Authorization'] = `Bearer ${tool.getLocal('dmbID')}`;
  }

  return config;
}, error => {

  return Promise.reject(error);
});

//添加一个返回拦截器
axios.interceptors.response.use(config => {
  if(config.data.status == 403){
    tool.removeLocal('dmbID')
    store.commit("setUserInfo", null);
    router.replace({
        name: 'index'
    })
  }
  return config;
}, error => {

  return Promise.reject(error);
});

